
body{
  background-color: white;
  padding: 0;
  margin: 0;
}
.button {
  background-color: red;
  color: white;
}


.wrap {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: red;
}
.center{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
  background-color: #aaff34;
  margin-bottom: 0.55rem;
}
.cell {
  background-color: antiquewhite;
  margin-top: 0.2rem;
}
.icon {
  height: 0.8rem;
  width: 0.8rem;
  background-color: beige;
}
.btn {
  height: 1rem;
  width: 1rem;
  background-color: #57ff24;
}
.footer {
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  height: 0.9rem;
  background-color: white;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10000;
  border-top: solid 0.01rem #dddddd;
}
.footer-wrap {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: #8F8F8F;
}
.footer-wrap span {
  font-size: 0.2rem;
  margin-top: 0.05rem;
}
.selected {
  color: #33A3F4;
}


.ticon {
  margin-left: 8%;
  width: 15%;
  height: 15%;
  background-color: #8dff4a;
  display: inline-block;

}


